*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}
body{
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
}
a{
  text-decoration: none;
  color: black;
}

#root{
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.table{
  width: 10vw;
  height: 10vh;
  background-color: red;
}

.center{
  width: 400px;
  border: 1px solid black;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  padding: 20px 40px;
}

#username{
  width: 100%;
  border-radius: 5px;
  border: 1px solid black;
  padding: 10px;
  margin-bottom: 30px;
}

#email{
  width: 100%;
  border-radius: 5px;
  border: 1px solid black;
  padding: 10px;
  margin-bottom: 30px;
}

#name{
  width: 100%;
  border-radius: 5px;
  border: 1px solid black;
  padding: 10px;
  margin-bottom: 30px;
}

#lastname{
  width: 100%;
  border-radius: 5px;
  border: 1px solid black;
  padding: 10px;
  margin-bottom: 30px;
}

#password{
  width: 100%;
  border-radius: 5px;
  border: 1px solid black;
  padding: 10px;
  margin-bottom: 30px;
}

.fbuton{
  width: 100%;
  padding: 10px;
  margin-top: 30px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#sbutton{
  width: 30%;
  height: 35px;
  border-radius: 5px;
  border: 1px solid black;
}

.homec{
  width: 400px;
  border: 1px solid black;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  padding: 20px 40px;
}

.image{
  width: 90px;
  height: 90px;
  border-radius: 50%;
  border: 1px solid black;
  margin-left: calc(50% - 45px);
}

#funame{
  text-align: center;
  margin-top: 10px;
}

#fname{
  margin-top: 20px;
  text-align: center;
}

@media screen and (max-width: 600px) {
  .center{
    width: 100%;
    border: 0px solid black;
    border-radius: 0px;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 20px;
  }
}